@keyframes effectEntrada {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
        transform: scale(1.2);
    }
}

@keyframes effectFade {
    from {
        opacity: 0;
    }
}

@keyframes effectSubir {
    from {
        margin-top: 60px;
    }

}


@keyframes effectGiro {
    to{
        transform: rotate(180deg);
    }
}
@keyframes effectGiroFim {
    to {
        transform:  rotate(360deg);
    }
}

@keyframes EffectPulo {
    from{
        transform: scale(1);
    }
    to {
        transform: scale(1.5);
    }
}

@keyframes effectSaida {
    to {
        transform: scale(1);
    }
}